@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf");
}

body {
  margin: 0;
}

.search-bar {
  flex-direction: column;
  margin: 20px 8px;
  display: flex;
}

.search-bar__input {
  box-sizing: border-box;
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: Avenir Heavy, sans-serif;
  font-weight: 800;
}

.input--big {
  background-position: 25px 24px;
  background-size: 19px 19px;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 600;
}

.input--small {
  background-position: 13px 15px;
  background-size: 11px 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
  font-weight: 600;
}

.search-bar__label {
  position: relative;
}

.search-bar__label:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
}

.label--big:after {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.label--small:after {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.search-bar__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input::placeholder {
  color: #3d4e61;
  font-family: Avenir Book, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.input--small::placeholder {
  font-size: 14px;
}

.search-bar__input:focus {
  text-shadow: 0 4px 2px #3d4e6133;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  outline: none;
  box-shadow: 0 4px 4px #3d4e6133;
}

